<template>
  <div id="read">
    <nav-bar>
      <span slot="leftSpan">Books</span>
      <input 
        slot="center" 
        class="search" 
        type="text" 
        placeholder="ä¹¦å±±æœ‰è·¯å‹¤ä¸ºå¾„,å­¦æµ·æ— æ¶¯è‹¦ä½œèˆŸ" 
        v-model="inputValue" 
        @keyup.enter="addItem">
        <span slot="rightSpan">ğŸ“–</span>
    </nav-bar>
    <task-bar>
      <is-read 
      slot="tagging" 
      :todo="todo" 
      @reClick="change"
      :todoWord="['æœªçœ‹'+'('+todoCount+')','å·²çœ‹'+'('+doneCount+')']"></is-read>
      <span v-if="!todoCount && todo">å¿«æ¥æ·»åŠ ä½ æƒ³çœ‹çš„ä¹¦ç±å§</span>
      <span v-if="!doneCount && !todo">ä½ è¿˜æ²¡æœ‰çœ‹å®Œçš„ä¹¦ç±å“¦</span>
      <list-item 
      v-for="(item, index) in myBooks" 
      v-if="todo && !item.done" 
      :item="item.title" 
      :done="item.done"
      :key="index"
      @deleteItem="deleteItem(index)"
      @isDone="isDone(index)"></list-item>
      <list-item 
      v-for="(item, index) in myBooks" 
      v-if="!todo && item.done" 
      :item="item.title" 
      :done="item.done"
      :key="index"
      @deleteItem="deleteItem(index)"
      @isDone="isDone(index)"></list-item>
    </task-bar>
  </div>
</template>

<script>
  import NavBar from "components/common/navbar/NavBar.vue"
  import TaskBar from "components/content/taskbar/TaskBar.vue"
  import ListItem from "components/content/listItem/ListItem.vue"
  import IsRead from "components/common/isRead/IsRead.vue"

  import {getData, saveData, deleteData} from "assets/js/todoList.js"

  export default {
    name: "Read",
    components: {
      NavBar,
      TaskBar,
      ListItem,
      IsRead
    },
    data() {
      return {
        todo: true,
        myBooks: [],
        inputValue: "",
      }
    },
    computed: {
      //æœªå®Œæˆæ•°é‡
      todoCount() {
        return this.myBooks.filter(item => {
          return !item.done
        }).length
      },

      //å·²å®Œæˆæ•°é‡
      doneCount() {
        return this.myBooks.filter(item => {
          return item.done
        }).length
      }
    },
    // é¡µé¢åŠ è½½å³æ¸²æŸ“æ‰€æœ‰æ•°æ®
    created() {
      let allList = getData("myBooks")
      this.myBooks = allList
    },
    // ç›‘å¬æ•°æ®å˜åŒ–
    watch: {
      myBooks: {
        handler() {
          saveData(this.myBooks,"myBooks")          
        },
        deep: true //æ·±ç›‘å¬
      }
    },
    methods: {
      addItem(e) {
        let data = e.target.value
        if(data === "") {
          return alert("è¯·è¾“å…¥æƒ³çœ‹çš„ä¹¦ç±")
        }
        this.myBooks.push({title: data, done: false})
        this.inputValue = ""       
      },
      change() {
        this.todo = !this.todo
      },
      deleteItem(index) {
        this.myBooks.splice(index, 1)
      },
      isDone(index) {
        this.myBooks[index].done = !this.myBooks[index].done
      }
    },
  }
</script>

